.Portfolio {
    width: 100%;
    height: 1880px;
    display: flex;
    justify-content: center;
    align-items: start;
    background-color: #020617;
}

.Portfolio-inner {
    width: 1100px;
    height: 1880px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
}

.portfolio-header {
    font-style: normal;
    font-weight: 700;
    font-size: 42px;
    line-height: 64px;
    /* identical to box height, or 152% */
    text-align: center;
    color: #F3F4F6;
    width: 262px;
    height: 64px;
}

.portfolio-text {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    /* or 169% */
    text-align: center;
    color: #9CA3AF;
    width: 461px;
    height: 81px;
}

.portfolio-up {
    margin-top: 30px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 180px;
}

.portfolio-down {
    width: 100%;
    height: 1700px;
    display: flex;
    justify-content: space-between;
    align-items: start;
    flex-wrap: wrap;
}

.item {
    width: 420px;
    height: 450px;
    background: linear-gradient(0deg, #323443, #323443), #EFF6FF;
    /* background: #111827; */
    border-radius: 20px;
}

.item-img {
    background-image: url(../../assets/myphoto.jpg);
    background-size: cover;
    height: 60%;
    border-radius: 20px 20px 0 0;
}

.item-bottom {
    display: flex;
    height: 200px;
    flex-direction: column;
    text-align: center;
    justify-content: start;
    align-items: center;
    border-radius: 0px 0px 20px 20px;
    background-color: #201944;
}

.item-header {
    letter-spacing: 2px;
    margin-top: 40px;
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 27px;
    color: #ffffff;
}

.item-description {
    letter-spacing: 1px;
    margin-top: 10px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: #ffffff;
}

.item-bottom-details {
    margin-top: 30px;
    width: 88%;
    display: flex;
    justify-content: space-between;
}

.item-bottom-details-img {
    width: 30px;
    height: 30px;
    margin-left: 5px;
}

.item-bottom-details-item {
    width: 165px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #f7df5a;
    background-color: #1f3d80;
    border-radius: 14px;
    transition: all .1s;
    cursor: pointer;
}

.item-bottom-details-item:hover {
    background-color: #172e5f;
}

.item-bottom-details-item:active {
    background-color: #214185;
}

.item-bottom-details-text {
    margin-right: 5px;
    width: 110px;
    height: 30px;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 17px;
    transition: all .1s;
}